.base {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  width: 100%;
  height: 420px;
}

.header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 20px;
  padding: 5px;
}

.header span {
  font-size: 12px
}

.header label {
  font-size: 12px
}

.header input[type=number] {
  width: 20%
}

.form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  width: 200px
}
